import todolist from '../images/todolist.jpg'
import defPic from '../images/wrong_tab.png'
import writting from '../images/writting.jpg'
import done from '../images/done.jpg'
import regis from '../images/registration.jpg'
import login from '../images/login.jpg'

import './../styles/header.css'

import { Switch, Route } from 'react-router-dom'



const Header = () => {
    return (
        <Switch>
            <Route path="/" exact render={()=><img src={todolist} alt=""/>} />
            <Route path="/new" render={()=><img src={writting} alt=""/>} />
            <Route path="/trash" render={()=><img src={defPic} alt=""/>} />
            <Route path="/search" render={()=><img src={done} alt=""/>} />
            <Route path="/register" render={()=><img src={regis} alt=""/>} />
            <Route path="/login" render={()=><img src={login} alt=""/>} />
        </Switch>

    )
}



export default Header;
