import React, { Component } from 'react';
import defaultPic from '../images/default.png'

class New extends Component {
    state = {
        name: "",
        description: "",
        mail: "",
        photoLink: "",
        warning: ""
    }
    
    nameChange = (event) => {
        this.setState({
            name: event.target.value
        })
    }

    descriptionChange = (event) => {
        this.setState({
            description: event.target.value
        })
    }

    mailChange = (event) => {
        this.setState({
            mail: event.target.value
        })
    }

    photoLinkChange = (event) => {
        this.setState({
            photoLink: event.target.value
        })
    }

    append = () => {

        if(this.state.name !== "" && this.state.description !== "" && this.state.mail !== "")
        {
            this.props.adder(this.state.name, this.state.description, this.state.mail, this.state.photoLink)
            this.setState({
                name: "",
                description: "",
                mail: "",
                photoLink: "",
                warning: ""
            })
        }
        else
        {
            this.setState({
                warning: "Niekompletne Dane"
            })
        }
    }

    render() {
        return (
        <>
            <picture style={{float: 'right' }}>
            <br/>
            <source srcSet={this.state.photoLink}/>
            <img src={defaultPic} style={{width: 150 , height: 150}}/>
            <br/>
            ^ twoje profilowe
            </picture>

            <h2> Imię </h2>
            <input
                type="text"
                name="NameField"
                value={this.state.name}
                onChange={this.nameChange}
            /> 
            <h2> Opis </h2>
            <input
                type="text"
                name="DescField"
                value={this.state.description}
                onChange={this.descriptionChange}
            /> 
            <h2> Email </h2>
            <input
                type="text"
                name="MailField"
                value={this.state.mail}
                onChange={this.mailChange}
            />
            <h2> Link do profilówki </h2>
            <input
                type="text"
                name="LinkField"
                value={this.state.photoLink}
                onChange={this.photoLinkChange}
            />    
            <br/>
            <br/>
            <button 
                type="button"
                name="PushButton"
                onClick={this.append}
            >Dodaj zgłoszenie</button>
            <br/>
            <br/>
            <h2 style={{color : 'red'}}> {this.state.warning} </h2>
        </>
        );
    }
}

export default New;
