import React, { Component } from 'react';

import Student from './Student';


class ListOfSome extends Component {
    state = {
        search: "",
        list: ""
    }  

    ListOfSome = (event) => {
    
        this.setState({
            search: event.target.value
        })

        const tempList = this.props.students.filter(element => element.description.toLowerCase().includes(event.target.value.toLowerCase()));
    
        this.setState({

        list: tempList.map(it => (
            <Student
                name={it.name}
                description={it.description}
                mail={it.mail}
                photoLink={it.photoLink}
            />
        ))

        })
       
    }

    
    render() {
        return (
        <>
            <h2>Szukaj:</h2>
            <input
                type="text"
                name="SearchField"
                value={this.state.search}
                onChange={this.ListOfSome}
            /> 

            <ul>
            {this.state.list}
            </ul>
        </>
        );
    }

}


export default ListOfSome;
