import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';

import ListOfAll from './ListOfAll.js'
import New from './New.js'
import Sekret from './sekret.js'
import ListOfSome from './ListOfSome.js'
import { addToBase, getBase } from '../firebase.js';
import Login from './login';
import Register from './register';

class Main extends Component {
  state = {
    listOfStudents: getBase()
  }

  addNew = (name, description, mail, photoLink) => {
    const newId = this.state.listOfStudents.length;
    this.setState({
      listOfStudents: this.state.listOfStudents.concat({name: name, description: description,mail: mail,photoLink: photoLink})
    })
    addToBase(name, description, mail, photoLink);
  }

  componentDidMount = () => {
    //jaki jest sens zycia?
  }

  render() {
    return (

      <Switch>
        <Route path="/" exact>
          <section><ListOfAll students={this.state.listOfStudents}/></section>
        </Route>
        <Route path="/new">
          <section><New adder={this.addNew}/></section>
        </Route>
        <Route path="/search">
          <section><ListOfSome students={this.state.listOfStudents}/></section>
        </Route>
        <Route path="/login">
          <section><Login /></section>
        </Route>
        <Route path="/register">
          <section><Register /></section>
        </Route>
        <Route path="/trash">
          <section><Sekret /></section>
        </Route>
        <Route>
          <section><h1>Nie mamy takiej podstrony</h1></section>
        </Route>
      </Switch>
    );
  }
}

export default Main;
